import React from 'react';
import axios from 'axios';
import Toolbar from '../components /Toolbar/Toolbar';
import './Css/Login.css';

class Login extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            username:"",
            pass:""
        }

        this.onSubmit = this.onSubmit.bind(this);
        this.onChangeUser = this.onChangeUser.bind(this);
        this.onChangePass = this.onChangePass.bind(this);
    }

    onChangeUser(e){
        this.setState({ username : e.target.value})
    }

    onChangePass(e){
        this.setState({ pass: e.target.value })
    }

    onSubmit(e){
        e.preventDefault();
        const jsonLogIn = {
            username : this.state.username,
            password : this.state.pass
        };

        axios.post('https://www.tiendatotem.com/api/Auth/login', jsonLogIn)
            .then((res) => {
                console.log(res)
                localStorage.setItem('nameRol', res.data.rol.name);
                localStorage.setItem('nameUser', res.data.name);
                localStorage.setItem('surNameUser', res.data.surName);
                localStorage.setItem('idRol', res.data.rolId);
                localStorage.setItem('id', res.data.id);
                localStorage.setItem('login', true);
                localStorage.setItem('pageName', res.data.rol.name);
                this.props.changeRol(res.data.rol.name);
            }).catch((error) => {
                console.log(error)
            });

        this.setState({ name: '', email: '' })
    }

    render(){
        return(
            <div className="container-login">
                <Toolbar drawerClickHandler={null} withDrawer={false}/> 
                <main className="main-login">
                    <form className="form-body">
                        <p className="spacer text-title">Bienvenido a Totem Store por favor inicie sesión</p>
                        <input className="spacer field-login" type="text" placeholder="Nombre de usuario" onChange={this.onChangeUser}/>
                        <input className="spacer field-login" type="password" placeholder="Contraseña" onChange={this.onChangePass}/>
                        <input className="btn-login spacer" onClick={this.onSubmit} type="submit" value="Iniciar sesion" />
                        <p className="text-subtitle">Si aún no tienes una cuenta</p>
                        <input className="btn-sign-here" value="Regístrate aquí" type="submit" onClick={() => this.props.changeRol("registro")}/>
                    </form>
                </main>
            </div>
        );
    }
}

export default Login;