import React from 'react';
import {Post,Put,Delete} from 'react-axios';
import InstanceAxios from './API';
import Loading from '../Pages/Loading';

const HOCPost = ({compOk : Ok,compError: Error, path, parameters}) => {
    return(
        <Post url={path} data={parameters} instance={InstanceAxios}>
            {
                (error, response,isLoading,_makeRequest,_axios) => {

                    if(error){
                        return(<Error />)
                    }
                    else if(isLoading){
                        return(<Loading/>)
                    }
                    else if(response != null){
                        return(<Ok/>)
                    }
                    return null

                }
            }
        </Post>
    )
}

