import React from 'react';
import ManageCatalogs from '../components /Cards/Catalog/ManageCatalogs';
import Line from '../components /UtilViews/Line';
import icCatalog from '../img/catalog.png';

import SideDrawer from '../components /SideDrawer/SideDrawerClientePage/SideDrawer';
import Backdrop from '../components /Backdrop/Backdrop';
import Toolbar from '../components /Toolbar/Toolbar';
import './Css/AdminPage.css';
import {
    BrowserRouter as Router,
    Switch,
    Route
} from 'react-router-dom';

const routes = [
    {
      path: "/",
      exact: true,
      main: () => <ManageCatalogs/>
    }
  ];

class ClientPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            sideDrawerOpen : false,
            namePage: "Catálogos"
        }
        this.setNamePage = this.setNamePage.bind(this);
    }

    setNamePage(namePage){
        this.setState({namePage:namePage});
    }

    drawerToggleClickHandler = () => {
        this.setState((prevState)=> {
          return {sideDrawerOpen:!prevState.sideDrawerOpen};
        });
    };
    
    backdropClickHandler = () => {
        this.setState({sideDrawerOpen:false});
    };

    render(){
        let backdrop;

        if(this.state.sideDrawerOpen){
            backdrop = <Backdrop backdropClickHandler={this.backdropClickHandler}/>
        }
        return(
            <Router>
                <Toolbar drawerClickHandler={this.drawerToggleClickHandler} withDrawer={true}/> 
                <SideDrawer show={this.state.sideDrawerOpen} 
                            changeRol={this.props.changeRol} 
                            toggle={this.drawerToggleClickHandler}
                            setNamePage={this.setNamePage}/>
                {backdrop}

                <main style={{marginTop: '64px', marginLeft: '10px', marginRight: '10px'}}>

                    <div className="container-page">

                        <div className="title-page">
                            <img src={icCatalog} alt="icon people"/>
                            <h4>Administrar {this.state.namePage}</h4>
                        </div>
                        <Line/>

                        <Switch>
                            {routes.map((route, index) => (
                                    <Route
                                        key={index}
                                        path={route.path}
                                        exact={route.exact}
                                        children={<route.main />}
                                    />
                                ))
                            }
                        </Switch>

                    </div>
                </main>
            </Router>
        );
    }
}

export default ClientPage;